const initialState = {
    status: 'idel',
    productIds:[],
    count: 0
}

const reducer = (state=initialState,action) => {
    switch (action.type) {
        case 'inc':
            
            return{
                ...state,
                count: state.count + 1,
                
            }
            
            case 'dec':
                
            return{
                ...state,
                count: state.count - 1
            }
            case 'Add_product_id':
                
            return{
                ...state,
                productIds: 
                    [...state.productIds,
                    {
                    id:action.id,
                    image: action.image,
                    title: action.title,
                    price: action.price
                }
            ],
            status: state.status = 'success'
            }
            case 'Remove_product':
                const newCart = state.productIds.filter(item => item.id !== action.id)
            return {
                ...state,
                productIds: [
                    ...newCart]
            }
            case 'Shop_successfull':
            return {
                ...state,
                productIds: [],
                count: 0
            }
            
    
        default:
            return state
    }
}

export default reducer